body {
    font-family: Arial, sans-serif;
    background-color: #656565;
    margin: 0;
    padding: 0;
    text-align: center;
}

.container {
    max-width: 800px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 80px;
}

h1 {
    text-align: center;
}

form {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

input, textarea, button {
    margin-bottom: 10px;
    padding: 10px;
    font-size: 16px;
}

button {
    background-color: #007BFF;
    color: #fff;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

.informe {
    border: 1px solid #ddd;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #d9ecb5;
    border-radius: 50px;
}

.informe h2 {
    margin: 0 0 10px;
}

.informe p {
    margin: 0 0 5px;
}

.informe button {
    margin-right: 5px;
}




.arriba{
    background-color: #000000;
}
    .barra{
        /* background-color: #333; */
        background-color: #000000;
        color: #fff;
        display: flex;
        justify-content: space-between;
        padding: 20px;
    }
        .logo{
            color: #fff;
            text-decoration: none;
            font-weight: bold;
            font-size: 24px;
        }
        .logo a{
            color: #fff;
        }
        .navegacion{
            display: flex;
            gap: 20px;
        }
            .navegacion a{
                color: #fff;
                text-decoration: none;
                font-weight: bold;
                font-size: 18px;
            }
            .iniciarSesion a{
                color: #fff;
            }

.logito{
    width: auto;
    height: 50px;
}

button{
    background-color: rgb(142, 36, 0);
    padding: 10px;
    border-radius: 25px;
}
button:hover{
    background-color: #333;
}

#fecha, #informe, #titulo, #searchInput{
    border-radius: 50px;
    background-color: #c1da91;
    color: #000000;
}

.comentarios{
    display: flex;
    flex-direction: column;
    background-color: #c1da91;
    border-radius: 50px;
    padding: 10px;
    margin-bottom: 10px;
    align-items: center;
    align-content: center;
}
.comentarioLabel{
    width: 50%;
    align-items: center;
    align-content: center;
    border-radius: 50px;
}
